body {
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', sans-serif;
  background-color: #0e0e0e;
  color: white;
  overflow-x: hidden;
}

.container {
  text-align: center;
  padding: 30px;
}

.title {
  font-size: 2.5rem;
  font-weight: bold;
  color: #00faff;
  text-shadow: 0 0 10px #00faff;
  margin-bottom: 40px;
}

.slider {
  display: flex;
  width: 200%;
  transition: transform 0.6s ease-in-out;
}

.show-embed {
  transform: translateX(0%);
}

.show-extract {
  transform: translateX(-50%);
}

.section {
  width: 50%;
  padding: 30px;
  box-sizing: border-box;
  transition: all 0.5s ease-in-out;
}

input[type="file"],
textarea {
  width: 80%;
  padding: 12px;
  margin: 15px auto;
  background-color: #1e1e1e;
  border: none;
  border-radius: 8px;
  color: white;
  display: block;
}

textarea {
  height: 120px;
  resize: none;
}

.output-box {
  height: 100px;
  background-color: #2a2a2a;
  color: #00ffb7;
  font-weight: bold;
  margin-top: 15px;
}

.action-btn {
  background-color: #00faff;
  color: black;
  border: none;
  padding: 12px 25px;
  border-radius: 25px;
  margin-top: 10px;
  cursor: pointer;
  font-weight: bold;
  transition: 0.3s;
}

.action-btn:hover {
  background-color: #00c2c2;
}

.arrow-btn {
  background: none;
  border: none;
  color: #00faff;
  font-size: 30px;
  margin-top: 30px;
  cursor: pointer;
  transition: transform 0.3s;
}

.arrow-btn:hover {
  transform: rotate(90deg) scale(1.2);
}
